<template>
  <div class="h-full text-content-on-neutral-primary">
    <FullCalendar ref="fullCalendar" :options="calendarOptions" class="h-full">
      <template v-slot:eventContent="arg">
        <GeneralTag
          :content="`${
            arg.event.extendedProps.status === 'COMPLETED' ? '' : ''
          } ${arg.timeText} ${arg?.event?.title}`"
          :type="arg?.event?.extendedProps?.type"
          :tag-type="'small'"
          class="text-wrap border-none text-center"
          :class="
            arg.event.extendedProps.status === 'COMPLETED'
              ? 'line-through opacity-70'
              : ''
          "
        />
      </template>
    </FullCalendar>
  </div>
</template>

<script>
import FullCalendar from "@fullcalendar/vue";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid"; // Import timeGrid plugin
import interactionPlugin from "@fullcalendar/interaction";
import dayGridYearPlugin from "@fullcalendar/daygrid"; // Make sure you have a Year view plugin (this is a placeholder)

export default {
  components: {
    FullCalendar,
    GeneralTag: () => import("@/components/General/Tag.vue"),
  },
  props: {
    events: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      calendarOptions: {
        plugins: [
          dayGridPlugin,
          timeGridPlugin,
          interactionPlugin,
          dayGridYearPlugin,
        ],
        initialView: "dayGridMonth",
        headerToolbar: {
          left: "prev,next today",
          center: "title",
          right: "dayGridYear,dayGridMonth,timeGridWeek,timeGridDay", // Add year view button
        },
        themeSystem: "standard",
        fixedWeekCount: false,
        weekends: true,
        events: [],
        eventClassNames: () => {
          let output = ["bg-transparent", "border-0", "mb-spacing-50"];
          return output;
        },
        dateClick: this.handleDateClick,
        eventClick: this.handleEventClick,
      },
    };
  },
  watch: {
    calendarEvents: {
      handler(newEvents) {
        this.calendarOptions.events = newEvents;
      },
      immediate: true, // Trigger handler on component mount
    },
  },
  computed: {
    calendarEvents() {
      let output = [];
      if (this.events?.length > 0) {
        output = this.events.map((event) => {
          if (event.type === "QUOTATION_FOLLOW_UP_EVENT") {
            return {
              title: `Follow Up: ${event?.marketResearchId?.name} | ${event?.companyId?.companyName}`,
              start: event?.eventDate,
              allDay: true,
              extendedProps: {
                type: event.status === "COMPLETED" ? "success" : "water",
                status: event.status,
                eventData: { ...event },
              },
            };
          } else if (event.type === "COMPANY_DETAILS_MISSING") {
            return {
              title: `Add Missing Details: ${event?.companyId?.companyName}`,
              start: event?.eventDate,
              allDay: true,

              extendedProps: {
                type: event.status === "COMPLETED" ? "success" : "fire",
                status: event.status,
                eventData: { ...event },
              },
            };
          }
        });
      }
      return output;
    },
  },
  methods: {
    handleDateClick(info) {
      // Change the view to the day view for the clicked date
      this.$refs.fullCalendar.getApi().changeView("timeGridDay", info.date);
    },
    handleEventClick(info) {
      const event = info?.event?.extendedProps?.eventData;
      if (event.type === "QUOTATION_FOLLOW_UP_EVENT") {
        this.$router.push({
          path: `/dashboard/market-researches/${event?.marketResearchId?._id}`,
          query: {
            companyId: event?.companyId?._id,
          },
        });
      } else if (event.type === "COMPANY_DETAILS_MISSING") {
        this.$router.push({
          path: `/dashboard/companies/${event?.companyId?._id}`,
          query: {
            edit: true,
          },
        });
      }
    },
  },
};
</script>
